import kebab from "assets/img/buttonIcons/kebab-menu.svg";
import MenuBox from "components/MenuBox";
import { withAuthButton } from "components/hoc/withAuthButton";
import { IMenuProps } from "interface/component";
import React, { ReactNode, forwardRef, useRef } from "react";

interface IKebabRef extends IMenuProps {
  isDisabledByAcl?: boolean;
  isIgnoreAccessLevel?: boolean;
}
const FileUploadMenuBox = forwardRef((props: IKebabRef, ref: any) => {
  const {
    id,
    panelClassName,
    buttonClassName,
    variant,
    onShowMenu,
    isDisabledByAcl,
    isIgnoreAccessLevel,
    ...primeReactProps
  } = props;

  const { disabled, model } = primeReactProps;

  const menuRef = useRef<any>(null);

  const onPanelAction = (e: React.SyntheticEvent) => {
    e.stopPropagation();
  };

  const onShowMenuOverlay = (e: React.SyntheticEvent) => {
    if (typeof onShowMenu === "function") {
      onShowMenu();
    }
    menuRef.current.toggle?.(e);
  };

  const renderPanel = (): ReactNode => {
    return (
      <div className="panel-container" onClick={onPanelAction}>
        <MenuBox
          {...primeReactProps}
          className={`${
            panelClassName ?? ""
          } menu-card-file-upload bg-white !max-w-fit flex items-center shadow-md border border-primary-gray-300 !rounded-md !px-1.5 !py-1.5`}
          model={model}
          ref={menuRef}
        />
      </div>
    );
  };

  let disabledBtn = disabled;
  if (!isIgnoreAccessLevel) {
    disabledBtn = isDisabledByAcl;
  }
  disabledBtn = disabled;

  return (
    <div className={`flex items-center max-w-fit ${disabledBtn ? "" : "cursor-pointer"}`}>
      <div className="relative">
        <button
          type="button"
          title="options"
          className={`w-6 flex justify-center bg-white ${
            variant !== "SECONDARY" ? "border border-primary-ptext-700" : ""
          }  rounded hover:bg-primary-ptext-300 ${buttonClassName}`}
          id={id}
          onClick={onShowMenuOverlay}
          disabled={disabledBtn}
        >
          <span className="w-6 h-6 select-none">
            <img src={kebab} alt="options" />
          </span>
        </button>
        {renderPanel()}
      </div>
    </div>
  );
});

// Memoization
export default withAuthButton(FileUploadMenuBox);

